@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.barra {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    padding-top: 3rem;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    margin: 0;
  }
}
.derecha .dark-mode {
  width: 3rem;
  filter: invert(100%);
}
.derecha .dark-mode:hover {
  cursor: pointer;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    padding-top: 2rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
  font-size: 2.2rem;
  position: relative;
  transition: all 0.5s ease;
}
.navegacion a::before {
  content: "";
  bottom: 0;
  left: 0;
  position: absolute;
  width: 0;
  height: 0.3rem;
  background-color: #E08709;
  transition: all 0.5s ease;
}
.navegacion a:hover {
  color: #E08709;
}
.navegacion a:hover::before {
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.mobile_menu {
  width: 5rem;
}
@media (min-width: 768px) {
  .mobile_menu {
    display: none;
  }
}

.mostrar {
  opacity: 1;
  visibility: visible;
  height: auto;
}